html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background: #d3e0e0;
  background: linear-gradient(180deg, #d3e0e0 0%, #ebd9b2 100%); }

.sectionAllign {
  padding: 0 16px; }

.navigation__list {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.navigation__item {
  display: inline;
  display: block;
  margin: 0 16px; }

.navigation__link {
  text-decoration: none;
  color: #1A936F; }

.site-header-scr {
  background-image: url("../assets/wind-farm.jpg");
  background-position: center;
  background-origin: border-box;
  background-repeat: no-repeat;
  height: 400px;
  max-width: 1024px;
  margin: 92px auto 0;
  width: 100%;
  margin-top: 92px;
  position: relative;
  z-index: -1; }

.kwadrat {
  background: rgba(23, 29, 29, 0.493);
  width: 200px;
  height: 150px;
  color: white;
  position: absolute;
  right: 100px;
  bottom: 0px;
  padding: 16px; }

.kwadrat__h5 {
  padding: 0;
  color: tomato;
  margin: 0; }

.kwadrat__contekst {
  margin: 0; }

.contentGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 16px;
  grid-row-gap: 16px; }

@media (max-width: 640px) {
  .contentGrid {
    grid-template-columns: 1fr; } }

h2 {
  background-color: #cccccc;
  color: white; }

h3 {
  color: brown; }

h4 {
  /*  background-color: aqua; */
  color: tomato; }

.linkTable {
  border-spacing: 15px; }

.linkTable__td {
  border: 1px solid black;
  padding: 5px; }

.linkTable__th {
  background-color: white; }

.main__section-zarzadzanie {
  text-align: center;
  color: #9ABD97; }

.about {
  /* position: relative; */ }

.box {
  width: 100px;
  height: 100px;
  background-color: yellowgreen;
  padding: 10px;
  border: 10px solid red;
  box-sizing: border-box;
  position: absolute;
  /* relative/absolute */
  top: 10px;
  left: 50%; }

.top {
  position: fixed;
  bottom: 10px;
  right: 10px;
  text-decoration: none; }

.navigation__link--bigger {
  font-family: "Ubuntu";
  font-size: 2.5rem;
  font-weight: bold; }

.navigation__link--smaller {
  font-family: "Ubuntu";
  font-size: 1.5rem;
  font-weight: bold; }

.menu {
  display: flex;
  justify-content: center;
  align-items: center; }

.menu__item {
  color: bisque;
  padding: 0 16px;
  margin: 0 8px;
  text-decoration: none; }

.main {
  width: 100%;
  max-width: 1024px;
  margin: 0px auto;
  padding-top: 16px; }

@media (max-width: 900) {
  .main {
    max-width: 600px; } }

.header__title {
  font-size: 3rem;
  color: tomato;
  text-align: center; }

.header__title--red {
  color: darkred; }

.myLinks {
  background: #edeeb7; }

.mainForm {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh; }

.myForm {
  max-width: 600px;
  width: 100%; }

.logo {
  background: white;
  border: 2px solid red; }

.hobby__list {
  list-style: none;
  padding: 0; }

.site-header {
  background: #fff;
  box-sizing: border-box;
  position: fixed;
  left: 0px;
  padding: 16px;
  position: fixed;
  top: 0;
  width: 100%; }

.homePageFooter {
  text-align: center; }

@media (min-width: 730px) {
  .navigation__switcher {
    display: none; } }

@media (max-width: 730px) {
  .navigation__list {
    display: none;
    flex-direction: column; }
  .navigation__list--visible {
    display: flex; }
  .site-header {
    min-height: 48px; }
  .navigation__switcher {
    border: solid 1px;
    background: none;
    position: absolute;
    right: 16px;
    top: 16px; } }
